.contactWrapper {
  padding: 40px 0;
  .contactHeader {
    > h4 {
      border-bottom: 1px solid #000;
      width: fit-content;
      padding-bottom: 5px;
    }
  }
  .contactContent {
    display: flex;
    flex-direction: column;
    gap: 50px;
    > :first-child {
      white-space: nowrap;
    }
    > :last-child {
      width: 100%;
    }
    @media (min-width: 769px) {
      flex-direction: row;
      justify-content: space-between;
      align-items: flex-start;

      > :last-child {
        max-width: 600px;
      }
    }
  }
  .formContent {
    button {
      margin-top: 20px;
      border: 1px solid #011521;
      color: #011521;
      font-weight: bold;
      &:hover {
        box-shadow: 0 0 1.1rem 8px #011521;
      }
      @media (max-width: 768px) {
        margin: 20px auto 0 auto;
      }
    }
  }
  .contactUsInfo {
    > span {
      display: flex;
      align-items: center;
      column-gap: 5px;
      > img {
        width: 20px;
      }
    }
  }
}
